@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
/* Google Font Link  */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.banner-main{
    background: url(/static/media/banner-2.067a9015.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    padding: 100px 0;
    position: relative;
    z-index: 1;
}

.banner-main::after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(30, 56, 81, 0.8);
    z-index: -1;
}

.cars-text-main{
    color: #fff;
}

.cars-text-main h1{
    color: #fff;
    font-size: 60px;
    color: #E7993E;
    font-weight: 800;
}
.cars-text-main h2{
    font-size: 35px;
    font-weight: 700;
}

.cars-text-main p{
    color: #f7b731;
    font-size: 18px;
    line-height: 1.7;
    font-weight: 500;
}

.common-button{
    background:#FC427B;
    color: #fff;
    padding: 14px 40px;
    display: inline-block;
    font-weight: 600;
    font-size: 18px;
    border-radius: 2px 25px 2px 25px;
}

.common-button:hover{
    color: #fff;
    background:coral;
}

@media (max-width:600px){
    .cars-text-main h1{
        font-size: 35px;
    }
    .cars-text-main h2{
        font-size: 22px;
    }
    .cars-text-main p{
        font-size: 14px;
    }
    .common-button{
        padding: 10px 25px;
        font-size: 15px;
    }
    .banner-right-img{
        margin-top: 30px;
    }
}
.not-found-page{
    padding: 40px 10px;
}
.not-found-page h1{
    font-size: 8rem;
    font-weight: 700;
    padding:40px 0 10px;
}

.not-found-page h1 span{
    color: orangered;
}
.not-found-page p{
    font-size: 5rem;
    font-weight: 600;
    padding-bottom:20px ;
}

.not-found-page .not-btn{
    margin-bottom: 20px;
}

.not-found-btn{
    display: inline-block !important;
    width: auto !important;
    padding: 12px 25px !important;
    border-radius: 5px 5px !important;
}

@media(max-width:600px){
    .not-found-page p{
        font-size: 2.5rem;
    }
}
.input-form-box input{
    width: 100%;
    padding: 12px 15px;
    margin: 12px 0;
    border-radius: 8px;
    outline: none;
    border: 2px solid #FC427B;
}
.log-title{
    color: #FC427B;
    font-weight: 600;
    font-size: 30px;
}

.input-form-box input[type="submit"]{
    background: #FC427B;
    color: #fff;
    font-size: 18px;
    font-weight: 600;
}
.nav-main{
    background: #FC427B;
}
.nav-item{
    font-size: 16px;
    font-weight: 500;
    color: #fff !important;
}
.log-out{
    background: #fff;
    color: #FC427B !important;
    border-radius: 5px;
    font-weight: 500;
}
.single-product-box{
    border-radius: 20px;
   padding:25px 20px;
}



.single-product-box h3{
    color: #eb3b5a;
    font-weight: 500;
    text-transform: capitalize;
}
.single-product-box h5{
    color: #ffffff;
    font-weight: 600;
    margin: 8px 0;
     text-transform: capitalize;
    font-size: 18px;
    background: #09D066;
    padding: 5px 10px;
    border-radius: 5px;
}
.single-product-box h4{
    background: #fbc531;
    padding: 10px 0;
    color: #4cd137;
    margin-bottom: 0;
    border-radius: 10px 10px 0 0;
}

.single-product-box p{
    color: #fff;
    line-height: 1.6;
    background: #9c88ff;
    padding: 8px;
    margin-bottom: 0;
}

.buy-now-btn{
    display: block;
    background: #FC427B;
    padding: 15px 0;
    font-weight: 700;
    font-size: 20px;
    color: #fff;
    border-radius: 0 0 10px 10px;
    transition: .4s;
}

.buy-now-btn:hover{
    color: green;
}

@media(max-width:600px){
    .buy-now-btn{
        padding: 10px 0;
        font-size: 16px;
    }
    .single-product-box h3{
        font-size: 18px;
    }
    .single-product-box p{
       font-size: 14px;
    }
    .single-product-box h4{
        font-size: 16px;
    }
}
.spinner{
    margin-top: 180px;
}
.head-title{
    font-size: 40px;
    font-weight: 600;
    color: #a55eea;
}

@media (max-width:600px){
    .head-title{
        font-size: 22px;
    }
}
ul{
    padding-left:0 ;
}
ul li{
    list-style: none;
}

.footer-box{
    background: #FC427B;
    color: #fff;
}

.footer-head-title{
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 20px;
}

.footer-list li{
    font-size: 18px;
    font-weight: 400;
    margin: 10px 0;
}

.footer-list li a{
    color: #fff;
    font-weight: 500;
    text-decoration: none;
}

.footer-list li a:hover{
    text-decoration: underline;
}

.footer-text{
    border-top: 2px solid #fff;
}

.footer-contact li{
    display: inline-block;
}

.footer-contact li i {
    margin: 0 8px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    background: #fff;
    color: #FC427B;
    text-align: center;
    font-size: 20px;
    border-radius: 50%;
}

.footer-contact li i:hover{
    background: olivedrab;
    color: #fff;
}

@media(max-width:600px){
    .footer-head-title{
        font-size: 18px;
    }
    .footer-list li{
        font-size: 14px;
    }
    .footer-text p{
        font-size: 14px;
    }
}
.dashboard-link
{
    text-align: center;
    background: #3867d6;

}
.dashboard-link a
{
    font-size: 18px;
    font-weight: 600px;
    display: block;
    text-decoration: none;
    padding: 20px 0;
    color: #fff;
    border-top: 2px solid #ddd;
}

.dashboard-link a:hover{
    background: #a55eea;
}
.my-or{
    border-top: 0 !important;
}

.dash-title{
    border-bottom: 2px solid #fff;
    margin-bottom: 0;
}
.add-service-form{
    width: 40%;
    margin: 0 auto;
    padding: 30px;
}
.add-service-form input,.add-service-form textarea{
    width: 100%;
    padding: 12px 15px;
    margin: 12px 0;
    border-radius: 8px;
    outline: none;
    border: 2px solid #FC427B;
}
.add-service-form textarea{
    resize: none;
}

.add-service-form input[type="submit"]{
    background: #FC427B;
    border: 2px solid #adff2f;
    color: #fff;
    font-weight: 600;
}

.add-service-form input:focus,.add-service-form textarea:focus{
    border-color: #adff2f;
}

@media(max-width:600px){
    .add-service-form{
        width: 95%;
        padding: 10px;
    }
}
